body {
  background-color: #fff;
  font-family: Open Sans, sans-serif; }

.hero {
  display: block;
  padding-top: 76px;
  padding-bottom: 134px;
  transition: all .2s ease-in-out; }

.option-1 .hero {
  background-image: url(../img/background.png);
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #1a1a1c; }

.option-2 .hero {
  background-image: url(../img/background-red.png);
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #f1405c; }

.option-3 .hero {
  background-color: #2d2e31;
  padding-bottom: 80px; }

.variation-switcher {
  list-style: none;
  margin: 0;
  padding: 0;
  position: fixed;
  right: 30px;
  top: 26px;
  z-index: 5; }

.variation-switcher li {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin: 0 4px;
  background-color: #3e3e42;
  box-shadow: 0 0 7px 1px rgba(0, 0, 0, 0.11);
  cursor: pointer;
  transition: all .2s ease-in-out; }

.variation-switcher li:hover {
  background-color: #fff; }

.variation-switcher li.active {
  background-color: #868686; }

.cover-panel {
  width: 100%;
  box-shadow: 0 0 37.4px 5.6px rgba(0, 0, 0, 0.28);
  transition: all .2s ease-in-out;
  position: relative; }

.option-1 .cover-panel {
  background-color: #f1405c; }

.option-2 .cover-panel {
  background-color: #fff; }

.option-3 .cover-panel {
  background-color: #fff; }

.cover-panel-wrapper {
  padding-left: 20px;
  padding-right: 40px;
  padding-top: 30px;
  padding-bottom: 30px; }

.cover {
  padding-left: 0;
  position: relative; }

.cover img {
  position: relative;
  z-index: 1; }

.cover-content {
  transition: all .2s ease-in-out; }

.option-1 .cover-content {
  color: #fff; }

.option-2 .cover-content {
  color: #2d2e31; }

.option-3 .cover-content {
  color: #2d2e31; }

.cover-content h1 {
  font-family: Tiempos,Georgia,serif;
  margin: 0px;
  font-size: 32px;
  line-height: 38px; }

.cover-content span {
  opacity: 0.6;
  margin-bottom: 30px;
  font-size: 18px; }

.cover-content p {
  font-size: 20px;
  margin-bottom: 40px; }

.pill-button {
  text-align: center;
  line-height: 54px;
  width: 246px;
  height: 54px;
  border-radius: 27px;
  box-shadow: 0 0 23.5px 3.5px rgba(0, 0, 0, 0.07);
  transition: all .2s ease-in-out;
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 20px; }

.pill-button:hover {
  opacity: 0.6; }

.pill-button.light {
  color: #2d2e31;
  background-color: #fff; }

.option-2 .pill-button.light {
  color: #fff;
  background-color: #2d2e31; }

.option-3 .pill-button.light {
  color: #fff;
  background-color: #2d2e31; }

.pill-button.dark {
  color: #fff;
  background-color: #2d2e31; }

.content-box .pill-button.dark {
  margin: 0 auto;
  margin-top: 80px;
  display: block; }

.option-2 .pill-button.dark {
  color: #fff;
  background-color: #f1405c; }

.option-3 .pill-button.dark {
  color: #fff;
  background-color: #f1405c; }

.cover-content .pill-button:last-child {
  margin-left: 30px; }

.best-seller-button {
  width: 96px;
  height: 96px;
  border-radius: 50%;
  background-color: #f1405c;
  border: 4px solid white;
  box-shadow: 0 0 37.4px 5.6px rgba(0, 0, 0, 0.28);
  position: absolute;
  left: -48px;
  right: auto;
  top: -48px;
  text-align: center;
  color: white;
  font-size: 15px;
  font-weight: 500;
  line-height: 91px;
  z-index: 2;
  transform: rotate(-14deg);
  transition: all .2s ease-in-out; }

.option-2 .best-seller-button {
  background-color: #2d2e31; }

.content {
  position: relative;
  z-index: 2; }

.content.excerpt p {
  font-family: 'PT Serif', serif;
  font-size: 18px; }

.content-box {
  background-color: #fff;
  margin-top: -60px;
  padding: 100px;
  display: block;
  width: 100%;
  position: relative;
  transition: all .2s ease-in-out; }

.option-3 .content-box {
  margin-top: 0px; }

.content-box-wrapper {
  overflow: hidden;
  position: relative;
  z-index: 1;
  transition: all .5s ease-in-out; }

.content-box h2 {
  line-height: 30px;
  margin-top: 0;
  margin-bottom: 6px; }

.content-box .date {
  margin-bottom: 30px;
  display: block; }

.content-box p {
  color: #1a1a1d;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.7em;
  margin-top: 0; }

.intro .content-box p {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.9em; }

.content-box p:last-child {
  margin-bottom: 0; }

.read-more-button, .read-less-button {
  display: block;
  position: relative;
  z-index: 2;
  color: #f1405c;
  text-decoration: none; }

.read-less-button {
  padding-top: 40px; }

.read-more-button:before {
  content: "";
  position: absolute;
  height: 80px;
  top: -80px;
  width: 100%;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, white 100%); }

.reviews {
  position: relative;
  margin-top: -60px;
  z-index: 1;
  padding-top: 124px;
  padding-bottom: 124px;
  text-align: center;
  color: #fff;
  min-height: 348px;
  transition: all .2s ease-in-out; }

.option-1 .reviews {
  background-color: #f1405c; }

.option-2 .reviews {
  background-color: #2d2e31; }

.option-3 .reviews {
  background-color: #f1405c;
  margin-top: 0px;
  padding-top: 80px;
  padding-bottom: 80px; }

.review {
  text-align: center;
  max-width: 660px;
  width: 100%;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%);
  position: relative;
  padding-left: 20px;
  padding-right: 20px; }

.review p {
  margin: 0;
  display: inline-block;
  font-size: 30px;
  font-weight: 700;
  line-height: 36px; }

.review span {
  display: block;
  font-weight: 300;
  margin-top: 6px;
  font-style: italic; }

.orbit {
  width: 100%; }

.orbit-container {
  padding-left: 0px;
  height: 100%;
  max-height: 220px; }

.orbit-slide {
  height: 100%;
  position: relative; }

.site-footer {
  margin-top: -60px;
  height: 200px;
  z-index: 1;
  transition: all .2s ease-in-out; }

.option-1 .site-footer {
  background-color: #1a1a1c; }

.option-2 .site-footer {
  background-color: #f1405c; }

.option-3 .site-footer {
  background-color: #1a1a1c;
  margin-top: 0px;
  height: 140px; }

.site-footer .row {
  height: 100%;
  text-align: center; }

.site-footer p {
  font-size: 14px;
  margin-top: 62px;
  color: #fff;
  margin-bottom: 0;
  transition: all .2s ease-in-out; }

.option-3 .site-footer p {
  margin-top: 0px; }

.read-more-content,
.read-more {
  display: none;
  margin-top: 1rem; }

.read-more-content.expanded .read-more,
.read-more.expanded .read-more {
  display: block; }

@media only screen and (max-width: 1110px) {
  .best-seller-button {
    left: 1px; } }

@media only screen and (max-width: 926px) {
  .pill-button {
    width: 200px; } }

@media only screen and (max-width: 790px) {
  .cover-content .pill-button {
    width: 160px;
    font-size: 16px; }
  .cover-content .pill-button:last-child {
    margin-left: 20px; }
  .variation-switcher {
    right: 16px; }
  .variation-switcher li {
    display: inline-block;
    width: 16px;
    height: 16px;
    margin: 0 8px; } }

@media only screen and (max-width: 650px) {
  .cover-content .pill-button {
    width: 140px;
    font-size: 14px; }
  .cover-content .pill-button:last-child {
    margin-left: 20px; }
  .content-box {
    padding: 20px;
    padding-top: 40px;
    padding-bottom: 40px; } }

@media only screen and (max-width: 480px) {
  .variation-switcher {
    position: absolute; }
  .best-seller-button {
    left: 8px;
    right: auto; }
  .cover {
    padding: 0; }
  .cover-content .pill-button {
    width: 140px;
    font-size: 14px; }
  .cover-content .pill-button:last-child {
    margin-left: 10px; }
  .cover-panel-wrapper {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 50px;
    padding-bottom: 50px; }
  .reviews {
    padding-top: 80px;
    padding-bottom: 80px; }
  .review p {
    font-size: 20px;
    line-height: 26px; } }
